<template>
  <form :name="formName" :action="submitAction" :method="actionType" v-on:submit.prevent="onSubmit">
  <nitro-input  inputName="userName" description="User Name"></nitro-input>
  <nitro-input  inputName="password" description="Password"></nitro-input>
  <nitro-button v-on:clicked="login" text="Submit"></nitro-button>
  </form>
</template>

<script>
import NitroInput from './NitroInput.vue'
import NitroButton from './NitroButton.vue'
export default {
  components: {
    "nitro-input": NitroInput,
    "nitro-button": NitroButton
  },
  props: ['submitAction', 'formName', 'actionType'],
  data () {
    return {

    }
  },
  methods: {
    login: function () {
      alert("I was clicked");
    },
    onSubmit: function () {
      alert("I stopped submitting");
    }
  }
}
</script>

<style lang="scss">
nitro-login {

}
